html {
  scroll-behavior: smooth;
}

body{
    margin:0;
     background-color: #2f2f2f; 
    display: grid;
    font-family: 'Noto Serif SC', serif;
    font-size: 150%;
    color: #cacaca;
}

div#wrapper{
  height: 100vh;
}

h1,h3{
  margin-top: 0%;
  margin-bottom: 0%;
  text-align: center;
}

h2.scrollStart{
  margin-top: 0;
  text-align: center;
}

div.spacer{
  margin-top: 40px;
}

div#recent{
  height: 100vh;
}

div.skills{
  margin-top: 0;
  height: 100vh;
  text-align: center;
}

h2.skillHeadline{
  margin-top: 0;
}

p{
  /* background-color: #cacaca;
  color: aliceblue; */
  margin-top: 0%;
  text-align: center;
}

header.notFound{
    background-image: url(../img/9.svg);
    background-size: cover;
    background-position: center center; 
    height: 100vh; 
    width: 100vw;
}

 h1.notFound{
    display: grid;
    font-family: 'Noto Serif SC', serif;
}
  p.notFound{
      margin-top: 0;
      text-align: center;
      font-size: 20px;
      color: darkgrey;
  }

  a{
    color: #cacaca;
     text-decoration: none;
  }


  header{
    margin-bottom: 0%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 30px 10%;
  }

  ul{
      list-style-type: none;
  }

  
.nav__links {
    list-style: none;
  }
  
  .nav__links li {
    display: inline-block;
    padding: 0px 20px;
  }
  
  .nav__links li a {
    transition: all 0.3s ease 0s;
  }
  
  .nav__links li a:hover {
    transition: all 0.3s ease 0s;
    color :#0088a9}

    
    div#footer{
      background-color:#010302;
    }

    footer{
      margin-top: 50px;
      margin-bottom: 50px;
      text-align: center;
    }